$cscecpl: "cscec-pl";

.#{$cscecpl}-header{
  box-sizing: border-box;
  position: fixed;
  top: 0;
  display: table;
  width: 100%;
  padding: 8px 16px;
  min-height: 0.64rem;
  height: 0.64rem;
  background-color: var(--blue-hd);
  color: #fff;
  box-shadow: 0 5px 10px rgba(0,0,0,.1), inset 0 -1px 0 rgba(0,0,0,.1);
  z-index: 1001;
}
.#{$cscecpl}-nbbrand{
  display: table-cell;
  color: #fff;
  font-size: 0.18rem;
  vertical-align: middle;
  width: 1%;
  white-space: nowrap;
  text-decoration: none;
}
.#{$cscecpl}-nbbrand:hover{
  color: #fff;
}
.#{$cscecpl}-nbcontent{
  display:flex;
  height: 100%;
}
.#{$cscecpl}-nbgroup{
  display: flex;
  align-items: center;
  list-style: none;
  color: #fff;
  font-size: 0.14rem;
  margin-left: auto;
  margin-bottom: 0;

  a{
    color: #fff;
  }
}
.#{$cscecpl}-nbitem{
  display: inline-block;
  margin-left: 10px;
  cursor: pointer;
}

.user-img{
  width: 31px;
  height: 30px;
  border: 1px solid #fff;
  vertical-align: middle;
  outline: 0;
  outline-offset: 0;
  margin: 4px 10px 4px 0;
  padding: 0;
  overflow: hidden;
  border-radius: 20px;

  img{
    margin-left: -2px;
  }
}

.#{$cscecpl}-sidebar{
  position: fixed;
  top: 0;
  bottom: 0;
  width: 0.5rem;
  background: #fff;
  z-index: 999;
  overflow: hidden;
  -webkit-box-shadow: 0 5px 10px rgba(0,0,0,.1), inset 0 -1px 0 rgba(0,0,0,.1);
  box-shadow: 0 5px 10px rgba(0,0,0,.1), inset 0 -1px 0 rgba(0,0,0,.1);
}
.#{$cscecpl}-sidebar{
  transition: width 0.5s ease-in-out;
  -moz-transition: width 0.5s ease-in-out; /* Firefox 4 */
  -webkit-transition: width 0.5s ease-in-out; /* Safari 和 Chrome */
}
.#{$cscecpl}-sidebar:hover{
  width: 2.2rem;
}
.sidebar-row{
  display: block;
  height: 0.5rem;
  line-height: 0.5rem;
  width: 2.2rem;
  color: #000;
  border-bottom: 1px solid #eee;
}
.sidebar-row:hover{
  background-color: var(--blue-hd);
  color: #fff;

  .sidebar-icon{
    color: #fff;
  }
}
.sidebar-icon{
  width: 0.5rem;
  color: #000;
}
.#{$cscecpl}-section{
  height: calc(100vh - 70px);
  overflow-y: auto;
}
.#{$cscecpl}-jccontainer{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.#{$cscecpl}-row{
  position: relative;
  height: auto;
  margin-right: 0;
  margin-left: 0;
  zoom: 1;
  display: block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.hide{
  display: none;
}
.mt-20{
  margin-top: 0.2rem;
}