.slide-container{
  height: 1.25rem;
}
.slide-table{
  height: 100%;
  width: 100%;
  table-layout:fixed;
  word-break:break-all;
  word-wrap: break-word;

  th{
    font-weight: normal;
  }
  .arrow-th{
    width: 0.36rem;
    // background: var(--teal)
  }
  .arrow-wrapper{
    height: 1.25rem;
    line-height: 1.25rem;
    text-align: center;
  }
  .arrow-wrapper img{
    height: 0.32rem;
    cursor: pointer;
  }
  .img-prev{ margin-left: -0.1rem; }
  .img-next{ margin-left: 6px; }
  .slide-th{
    width: calc(100% - 80px);
    // background: var(--info);
  }
}
.slide-quyu{
  height: 1.25rem;
  overflow: hidden;
}
.slide-box-group{
  display: flex;
  transition: all 0.5s cubic-bezier(0.42,0,0.58,1);
  -moz-transition: all 0.5s cubic-bezier(0.42,0,0.58,1); /* Firefox 4 */
  -webkit-transition: all 0.5s cubic-bezier(0.42,0,0.58,1); /* Safari 和 Chrome */

  .slide-box{
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    height: 1.25rem;
    width: 1.25rem;
    min-width: 1.25rem;
    border-radius: 5px;
    margin-right: 0.2rem;
    -webkit-box-shadow: 5px 0px 10px rgba(0,0,0,.12);
    box-shadow: 5px 0px 10px rgba(0,0,0,.12);
    position: relative;
    cursor: pointer;
  }
  .slide-box:last-child{
    margin-right: 0;
  }
  .bg-orange{
    background: url('../images/block-orange.png');
    background-size: 100% 100%;
  }
  .bg-deep-blue{
    background: url('../images/block-deep-blue.png');
    background-size: 100% 100%;
  }
  .bg-blue{
    background: url('../images/block-blue.png');
    background-size: 100% 100%;
  }
  .bg-green{
    background: url('../images/block-green.png');
    background-size: 100% 100%;
  }
  .bg-red{
    background: url('../images/block-red.png');
    background-size: 100% 100%;
  }
  .fz16{
    font-family: "Lantinghei SC", "Microsoft YaHei", "微软雅黑", SimSun, Arial;
    font-size: 0.16rem;
    font-weight: 500;
    color: #fff;
    margin-bottom: 0;
  }
  .fz32{
    font-size: 0.32rem;
    font-weight: 500;
    color: #fff;
    margin-bottom: 0;
  }
  .box-sr{
    position: absolute;
    right: 0.15rem;
    bottom: 0.15rem;
    font-size: 0.42rem;
    color: #fff;
  }
  .box-sr svg{
    opacity: 0.45;
  }
  .slide-box:hover .box-sr svg{
    opacity: 1;
  }
}
