@media screen and (min-width: 576px) {
  .sm-plcol-12{ width: 100%; }
  .sm-plcol-6{ width: 50%; }
}
@media screen and (min-width: 768px) {
  .md-plcol-12{ width: 100%; } 
  .md-plcol-6{ width: 50%; }
}
@media screen and (min-width: 992px) {
  .lg-plcol-12{ width: 100%; } 
  .lg-plcol-6{  width: 50%; }
}
.jc-plbox{
  background: transparent;
  margin-top: 5px;
  padding: 0 10px;
  float: left;

  .box-b{
    background: #fff;
    border: 1px solid #eee;
    // border-radius: 2px;
    height: 315px;
    min-height: 3rem;
    padding: 0.2rem;
    box-shadow: 0 2px 8px 0 rgba(0,0,0,0.08);
    font-weight: normal;
  }
  .box-head {
    color: #263250;
    height: 0.35rem;
    white-space: nowrap;
    overflow: hidden;
  }
  .box-l {
    height: 0.35rem;
    float: left;
    font-size: 0.18rem;
    font-weight: 600;
  }
  .box-l img{
    height: 0.24rem;
    width: auto;
    vertical-align: top;
    margin-top: 1px;
  }
  .box-titleicon{
    box-sizing: border-box;
    display: inline-block;
    height: 0.24rem;
    width: 0.24rem;
    background-repeat: no-repeat;
    background-size: 24px 24px;
    vertical-align: top;
    margin-top: 2px;
  }
  .box-title {
    margin-left: 10px;
  }
  .more-link{
    cursor: pointer;
    text-decoration: none;
    color: #263250;
    float: right;
  }
}